import React, { useContext, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import WebContext from '../../../../application/contexts/WebContext';
import { useParams } from 'react-router-dom';

import ClientApis from '../../APIs/ClientApis';
import Global from '../../../../infrastructure/Common/Global';

import { ReactComponent as AssetLanguage } from "./../../../../assets/icons/language.svg";
import { ReactComponent as AssetTick } from "./../../../../assets/icons/tick.svg";


import './course-detail.scss';

const CourseDetail = () => {
    const { t } = useTranslation();
    const webCtx = useContext(WebContext);
    const params = useParams();


    const [isLoading, setIsLoading] = useState(true);
    const [course, setCourse] = useState({});

    const baseURL = Global.baseUrl + "/";

    const languages = [
        { key: "en", value: 'English' },
        { key: "bg", value: 'Bulgarian' }
    ];

    useEffect(() => {
        if (params.courseId) {
            getCourse(params.courseId);
        }
    }, []);

    const getCourse = (courseId) => {
        ClientApis.getCourse(courseId)
            .then(res => {
                setCourse(res.data.result);
                // setTimeout(() => {
                setIsLoading(false);
                // }, 4000);
            })
            .catch(err => {
                console.log(err);
                setIsLoading(false);
            })
    };

    return (
        <div className="">

            <section className="course-meta py-5">
                <div className="container-xxl">
                    <div className="row">
                        <div className="col-md-8" data-aos="fade-up" data-aos-duration={500} data-aos-easing="linear" data-aos-delay={500}>
                            {isLoading ?
                                <div className="content-placeholder">
                                    <h1 className="placeholder-glow mb-4">
                                        <span className="placeholder col-11 h-25"></span>
                                        <span className="placeholder col-5 mt-3"></span>
                                    </h1>
                                    <p className="card-text placeholder-glow">
                                        <span className="placeholder col-12 mt-3"></span>
                                        <span className="placeholder col-6"></span>
                                        <br />
                                        <span className="placeholder col-5 my-3"></span>
                                        <br />
                                        <span className="placeholder col-4"></span>

                                    </p>
                                </div>
                                :
                                <React.Fragment>
                                    <h1 className="title fw-medium mb-0">{course['title_' + webCtx.appLang]}</h1>
                                    <div className="my-3">{course['short_desc_' + webCtx.appLang]}</div>

                                    <div className="languages my-3 max-text">
                                        <AssetLanguage className="me-2" />
                                        {t('taught_in_langs')}:

                                        {course.languages?.map((lang, i) => (
                                            <React.Fragment key={i}>
                                                <span className="underline mx-2">{languages.find(langObj => langObj.key === lang)?.value}</span>
                                                {i !== course.languages.length - 1 && '|'}
                                            </React.Fragment>
                                        ))}
                                    </div>

                                    <div className="languages my-3 max-text">
                                        {t('instructor')}:

                                        {course.instructors?.map((item, i) => (
                                            <React.Fragment key={i}>
                                                <span className="underline mx-2">{item}</span>
                                                {i !== course.instructors.length - 1 && '|'}
                                            </React.Fragment>
                                        ))}
                                        {/* <span className="underline mx-2">JP-Morgan</span>|
                                <span className="underline mx-2">Clinn Delev</span> */}
                                    </div>
                                </React.Fragment>
                            }
                        </div>
                        <div className="col-md-4" data-aos="fade-up" data-aos-duration={500} data-aos-easing="linear" data-aos-delay={1000}>
                            <div className="img-container">

                                {isLoading ?
                                    <div className="content-placeholder">
                                        <h5 className="w-100 min-h-200 placeholder-glow mt-2 me-3">
                                            <span className="placeholder col-12 min-h-250"></span>
                                        </h5>
                                    </div>
                                    : <div className="course-img" style={{ backgroundImage: `url('${baseURL + course.featured_img}')` }}></div>}

                            </div>
                        </div>
                    </div>
                </div>
            </section>

            <section className="py-5">
                <div className="container-xxl">
                    <div className="" data-aos="fade-up" data-aos-duration={500} data-aos-easing="linear" data-aos-delay={1500}>
                        <h4 className="max-text my-3">{t('what_you_will_learn')}</h4>
                        <div className="row">
                            <div className="col-md-8">
                                <div className="row">
                                    {course.key_features?.map((feature, i) => (
                                        <div className="col-md-6 mb-3 d-flex align-items-start" key={i}>
                                            <AssetTick className="me-2 max-text flex-shrink-0 mt-1" />
                                            <span>
                                                {feature[`feature_${webCtx.appLang}`]}
                                            </span>
                                        </div>
                                    ))}
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="mt-5" data-aos="fade-up" data-aos-duration={500} data-aos-easing="linear" data-aos-delay={1500}>
                        <h4 className="max-text my-3">{t('skills_you_will_gain')}</h4>
                        <div className="row">
                            <div className="col-md-8">
                                <div className="d-flex flex-wrap">
                                    {course.skills?.map((skill, i) => (
                                        <div className="nodis-tag me-2 mb-2 text-nowrap" key={i}>{skill[`skill_${webCtx.appLang}`]}</div>
                                    ))}
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="mt-5" data-aos="fade-up" data-aos-duration={500} data-aos-easing="linear">
                        <h4 className="max-text my-3">{t('about')}</h4>
                        <div className="row">
                            <div className="col-md-8">
                                {/* <div dangerouslySetInnerHTML={{ __html: course[`about_${webCtx.appLang}`] }} /> */}
                                {course[`about_${webCtx.appLang}`]}
                            </div>
                        </div>
                    </div>

                </div>
            </section>

        </div>
    );
};

export default CourseDetail;